<template>

<div class="invoice-container mt-3 border p-2"  ref="printArea">
 <loader :is-visible="isLoading" />
  <div class="text-center">
          <h4 style="margin-top: 0; color: #000; font-family: Arial, Helvetica, sans-serif; text-align: center; font-size: 18px; background: #f2f2f2; padding: 10px;">
            <span v-if="invoice == '0' || invoice == '' || invoice == null">Wallet Recharge Receipt</span>
            <span v-else>Invoice Payment Receipt</span> 
          </h4>
    </div> 



<div  style="width: 100%; display: flex; justify-content: space-between; align-items: center; padding-bottom: 5px; border-bottom: 1px dashed rgb(217, 216, 216); margin-bottom: 10px;">
  <img v-if="ReceiptData.company_logo" :src="ReceiptData.company_logo" style="width: 200px;" alt="Company Logo" />
  <div  class="div1" style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em;">
  <p style="font-weight:600; font-size:16px;">
  {{ ReceiptData.company_name }}
</p>
     <p>
  {{ ReceiptData.company_address || 'No Address Provided' }}
</p>

<p>
  {{ 
    (ReceiptData.company_zipcode || 'No Zip Code Provided') + 
    ', ' + 
    (ReceiptData.company_country || 'No Country Provided') 
  }}
</p>
  </div>
</div>

<div  style="clear: both;"></div>

<div class="header float-left w-100 p-2 mb-0">
  <div class="float-left col-md-6 p-0" style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em;">
         <p><strong>Client Name:</strong> <strong >{{ ReceiptData.firstname || '---' }}</strong></p>
         <p><strong>Address:</strong> <strong>{{ address }}</strong></p>
  </div>
   
  <div class="float-left col-md-6 p-0" style="color: rgb(0, 0, 0); font-family: Arial, Helvetica, sans-serif; font-size: 1em; display: flex; flex-direction: column; gap: 0px;">
      <p class="ps" style="display: flex; justify-content: end; align-items: center; white-space: nowrap;">
      <p v-if="invoice && invoice !== '0'" class="ps" style="display: flex; justify-content: end; align-items: center; white-space: nowrap;"> <b>Invoice Number:</b> {{ ReceiptData.bill_id || '---' }} </p>
  </div>
</div>

   
   <div style="clear: both;"></div>
  
    <table class="table table-bordered mt-0 border mb-2 secondtable">
      <tbody>
        <tr>
          
          <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background:rgb(249 249 249); width:400px;">Payment Method</td>
          <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background:rgb(249 249 249); width:400px;">Transaction ID</td>
          <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background:rgb(249 249 249); width:400px;">Payment Date</td>
          <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background:rgb(249 249 249)">Status</td>
        </tr>
      
        <tr>
          
          <td style="color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 1em;">{{ ReceiptData.payment_source || '---' }}</td>
          <td style="color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 1em;">{{ ReceiptData.txnid || '---' }}</td>
          <td style="color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 1em;">{{ ReceiptData.created_at ? $options.filters.formatDateTime(ReceiptData.created_at) : '---' }}</td>
          <td :style="{ color: ReceiptData.status && ReceiptData.status.toLowerCase() === 'success' ? 'green' : 'red' }" style="font-family: Arial, Helvetica, sans-serif; font-size: 1em;"> {{ ReceiptData.status || '---' }}</td>
        </tr>
      </tbody>
    </table>

    <table class="table table-bordered mt-0 text-right border float-right secondtable"  style="width:400px;">
      <tbody>
       <tr>
             <td style="font-weight: 600; color: #737373; font-family: Arial, Helvetica, sans-serif; font-size: 1em; background:rgb(249 249 249)">Total Amount</td>
             <td style="text-align:right; color: #000; font-family: Arial, Helvetica, sans-serif; font-size: 1.20em; font-weight:600;">Rs {{ (parseFloat(ReceiptData.previous_payment || 0) + parseFloat(ReceiptData.amount || 0)).toFixed(2) }}</td>
      </tr>
      </tbody>
    </table>

<div  style="clear: both;"></div>


 <p class="mt-1 fw-bold " :class="isPaymentSuccess ? 'alert alert-success text-center' : 'alert alert-danger text-center'">
      <span v-if="isPaymentSuccess">
        Thank you! Your transaction has been processed successfully.
      </span>
      <span v-else>
        Your transaction has failed. Please try again.
      </span>
    </p>
    
    <div class="text-right mt-3 no-print">
      <button class="btn btn-success mr-2" @click="PrintPaySlip">
        <em class="fa fa-print"></em> Print
      </button>
      <router-link class="btn btn-danger" :to="'/ClientBill/InvoiceListing'"> <em class="fa fa-close"></em> Close</router-link>
    </div>

  </div>
</template>



<script>
import DataService from '../../services/DataService';

export default {
  data() {
    return {
      txnid: '',
      invoice: '',
      CurrencyCode: '',
      isLoading: false,
      ReceiptData: {},
      address:''
    };
  },
  computed: {
    isPaymentSuccess() {
      if (!this.ReceiptData.status) return false;
      const status = this.ReceiptData.status.toLowerCase();
      return status !== 'failed' && status !== 'failure';
    }
  },
  created() {
    this.CurrencyCode = this.GetUserInfo.Currency.replace(/[()]/g, '');
    this.invoice = this.$route.query.invoice || '';
    this.txnid = this.$route.query.txnid || '';
    this.GetClientReceiptData();
  },
  methods: {
    
   formatFullAddress(data) {
  return [
    data.user_address_1,
    data.user_address_2,
    data.user_city,
    data.user_state,
    data.user_zipcode,
    data.user_country
  ]
    .filter(v => v && v.trim() !== '')
    .join(', ');
},
    PrintPaySlip() {
      this.$nextTick(() => {
        const el = this.$refs.printArea;
        if (!el) return;

        const printContents = el.innerHTML;
        const iframe = document.createElement("iframe");
        iframe.style.position = "absolute";
        iframe.style.width = "0";
        iframe.style.height = "0";
        iframe.style.border = "none";
        document.body.appendChild(iframe);

        const doc = iframe.contentWindow.document;
        doc.open();
        doc.write("<html><head><title>Print</title>");
        document.querySelectorAll("style, link[rel='stylesheet']").forEach(style => doc.write(style.outerHTML));
        doc.write("</head><body>");
        doc.write(printContents);
        doc.write("</body></html>");
        doc.close();

        iframe.contentWindow.focus();
        iframe.contentWindow.print();
        setTimeout(() => iframe.remove(), 500);
      });
    },
GetClientReceiptData() {
  this.isLoading = true;
  const url = `TransactionId=${this.txnid}`;

  DataService.GetClientReceiptData(url)
    .then(response => {

      if (!response.data) {
        this.ReceiptData = {};
        this.address = 'N/A';
        return;
      }

      const safe = (v) =>
        v !== null && v !== undefined && v !== '' ? v : '';

      let parsed = {};
      try {
        parsed = response.data.response_Json
          ? JSON.parse(response.data.response_Json)
          : {};
      } catch (err) {
        console.error("JSON parse error:", err);
      }

      if (this.$route.query.mode === 'RazorPay') {
        const attr = parsed.Attributes || {};

        this.ReceiptData = {
          bill_id: parsed?.Attributes?.notes?.billId || '---',

          company_logo: safe(response.data.company_Logo),
          company_name: safe(response.data.company_Name),
          company_address: safe(response.data.company_Address),
          company_city: safe(response.data.company_City),
          company_state: safe(response.data.company_State),
          company_country: safe(response.data.company_Country),
          company_zipcode: safe(response.data.company_ZipCode),

          user_address_1: safe(parsed.user_address_1),
          user_address_2: safe(parsed.user_address_2),
          user_city: safe(parsed.user_city),
          user_state: safe(parsed.user_state),
          user_zipcode: safe(parsed.user_zipcode),
          user_country: safe(parsed.user_country),

          firstname: attr.card ? attr.card.name : '',
          txnid: this.txnid,
          amount: attr.amount ? attr.amount / 100 : 0,
          status: attr.status === 'captured' ? 'Success' : 'Failed',
          payment_source: 'RazorPay',
          created_at: attr.created_at
            ? new Date(attr.created_at * 1000)
            : null
        };

        
        this.address = this.formatFullAddress(this.ReceiptData) || 'N/A';
        return;
      }

      let bill_id = '';
      if (parsed.udf2) {
        bill_id = parsed.udf2;
      } else if (parsed?.Attributes?.notes?.billId) {
        bill_id = parsed.Attributes.notes.billId;
      } else if (response.data.InvoiceId || parsed.InvoiceId) {
        bill_id = response.data.InvoiceId || parsed.InvoiceId;
      }

      this.ReceiptData = {
        bill_id: bill_id || '---',

        company_logo: safe(response.data.company_Logo),
        company_name: safe(response.data.company_Name),
        company_address: safe(response.data.company_Address),
        company_city: safe(response.data.company_City),
        company_state: safe(response.data.company_State),
        company_country: safe(response.data.company_Country),
        company_zipcode: safe(response.data.company_ZipCode),

        user_address_1: safe(parsed.user_address_1),
        user_address_2: safe(parsed.user_address_2),
        user_city: safe(parsed.user_city),
        user_state: safe(parsed.user_state),
        user_zipcode: safe(parsed.user_zipcode),
        user_country: safe(parsed.user_country),

        txnid: parsed.txnid || response.data.transaction_id || '',
        firstname: safe(parsed.firstname || parsed.client_name),
        payment_source: safe(parsed.payment_source),
        status: safe(parsed.status),
        amount: parsed.amount != null ? parsed.amount : '',
        created_at: response.data.created_At
          ? new Date(response.data.created_At)
          : null
      };

      this.address = this.formatFullAddress(this.ReceiptData) || 'N/A';
    })

    .catch(error => {
      console.error("Error fetching receipt data:", error);
      this.ReceiptData = {};
      this.address = 'N/A';
    })

    .finally(() => {
      this.isLoading = false;
    });
}


  }
};
</script>

<style scoped>

table.secondtable {
    width: 100%;
    border-collapse: collapse;
    margin-top: 10px;
}

table.secondtable th,
table.secondtable td {
    border: 1px solid #ddd!important;
    padding: 10px;
    text-align: left;
}

table.secondtable th {
    background-color: #f4f4f4;
}
</style>
